.greetingForm {
    display: flex;
    flex-direction: column;
    height: 200px;
    width: 200px;
}

.inputAndButtonContainer {
    width: 300px;
    height: 40px;
    display: flex;
    align-items: center;
}

.error {
    color : coral;
}

.input {
    border: 1px solid #525252; /* Добавьте границу для инпута */
    height: 36px;
    width: 200px;
    margin-right: 12px;
    border-radius: 6px;
}

.input:focus {
}

.errorInput {
    border: 1px solid coral;
    height: 36px;
    width: 200px;
    margin-right: 12px;
    border-radius: 6px;

}

.button {
    width: 53px;
    height: 36px;
    background-color : #0066cc;
    color : #FFFFFF;
    border-radius: 6px;
}

.button:hover {
    background-color : #3487da;
    cursor: pointer;
}

.button:active {
}

.button:disabled {
    background-color : #549de0;
}

.text {
    font-size: 14px;
}

